import React, { useState } from "react";
import sound from "../sounds/mixkit-message-pop-alert-2354.mp3";
import { Howl } from "howler";
import equals from "../sounds/mixkit-hard-pop-click-2364.wav";
import divide from "../images/Divide.png";
import back from "../images/Backspace (2).png";
import multiply from "../images/X.png";
import minus from "../images/Minus.png";
import plus from "../images/Plus.png";
import cancelAll from "../images/C.png";
import equalTo from "../images/Equals.png";
// import { set } from "immer/dist/internal";
// import back from "./images/Backspace.png"
// import back from "./images/Divide.png"

import nightModeBlackIcon from "../images/dark_mode_black_24dp.svg";
import nightModeWhiteIcon from "../images/dark_mode_white_24dp.svg";
import lightModeWhiteIcon from "../images/light_mode_white_24dp.svg";
import lightModeBlackIcon from "../images/light_mode_black_24dp.svg";

export default function Buttons({ theme, time, changeTheme }) {
  let darkStyles = {
    backgroundColor: "var(--mainColourDark)",
    color: "white",
  };
  let dayStyles = {
    backgroundColor: "var(--mainColour)",
  };

  const [input, setInput] = useState("");

  const callMySound = (src) => {
    const sound = new Howl({ src, html5: true });
    sound.play();
  };

  document.addEventListener("keypress", (event) => {
    press(event);
  });

  function press(event) {
    callMySound(sound);
    setInput((prevState) => {
      console.log("Event Target: ", event.target.id);
      // The conditional below checks if the last input and the new input
      // are both operators i.e not numbers.
      // if (
      //   (prevState[prevState.length - 1] === "*" ||
      //     prevState[prevState.length - 1] === "+" ||
      //     prevState[prevState.length - 1] === "-" ||
      //     prevState[prevState.length - 1] === "/") &&
      //   //-------------------------------------------\\
      //   (event.target.id === "*" ||
      //     event.target.id === "+" ||
      //     event.target.id === "-" ||
      //     event.target.id === "/")
      // ) {
      //   return prevState;
      // }

      return prevState + event.target.id;
    });
  }

  function getResult() {
    callMySound(equals);
    console.log("input: ", eval(input));
    if (input === "") {
      return;
    }
    setInput((prevState) => eval(prevState).toString());
  }
  function del() {
    callMySound(equals);
    setInput((prevState) => {
      return prevState.slice(0, -1);
    });
  }

  function cancel() {
    callMySound(equals);
    setInput("");
  }
  return (
    <>
      <div className="screens">
        <textarea className="screenInput" value={input} readOnly />
      </div>
      <div className="Buttons">
        <div className="buttonContainer">
          <button
            onClick={cancel}
            className="coloured"
            style={theme ? darkStyles : dayStyles}
          >
            {" "}
            <img src={cancelAll} />
          </button>
        </div>
        <div className="buttonContainer">
          <button
            id="**"
            onClick={press}
            className="coloured"
            style={theme ? darkStyles : dayStyles}
          >
            x^y{" "}
          </button>
        </div>
        <div className="buttonContainer">
          <button
            id="del"
            onClick={del}
            className="coloured"
            style={theme ? darkStyles : dayStyles}
          >
            {/* &lt;&lt;  */}
            <img src={back}></img>
          </button>
        </div>
        <div className="buttonContainer">
          <button
            id="/"
            onClick={press}
            className="coloured"
            style={theme ? darkStyles : dayStyles}
          >
            {/* <img src={divide} /> */} /
          </button>
        </div>
        <div className="buttonContainer">
          {" "}
          <button
            id="7"
            className="seven"
            onClick={press}
            style={theme ? darkStyles : dayStyles}
          >
            7{" "}
          </button>
        </div>
        <div className="buttonContainer">
          <button id="8" onClick={press} style={theme ? darkStyles : dayStyles}>
            8{" "}
          </button>
        </div>
        <div className="buttonContainer">
          <button id="9" onClick={press} style={theme ? darkStyles : dayStyles}>
            9{" "}
          </button>
        </div>
        <div className="buttonContainer">
          <button
            id="*"
            onClick={press}
            className="coloured"
            style={theme ? darkStyles : dayStyles}
          >
            {/* <img src={multiply}></img> */} x
          </button>
        </div>
        <div className="buttonContainer">
          <button id="4" onClick={press} style={theme ? darkStyles : dayStyles}>
            4
          </button>
        </div>
        <div className="buttonContainer">
          <button id="5" onClick={press} style={theme ? darkStyles : dayStyles}>
            5{" "}
          </button>
        </div>
        <div className="buttonContainer">
          <button id="6" onClick={press} style={theme ? darkStyles : dayStyles}>
            {" "}
            6{" "}
          </button>
        </div>
        <div className="buttonContainer">
          <button
            id="-"
            data-symbol="minusAttribute"
            onClick={press}
            className="coloured"
            style={theme ? darkStyles : dayStyles}
          >
            {/* <img src={minus}></img> */} -
          </button>
        </div>
        <div className="buttonContainer">
          <button id="1" onClick={press} style={theme ? darkStyles : dayStyles}>
            1{" "}
          </button>
        </div>
        <div className="buttonContainer">
          <button id="2" onClick={press} style={theme ? darkStyles : dayStyles}>
            2{" "}
          </button>
        </div>
        <div className="buttonContainer">
          <button id="3" onClick={press} style={theme ? darkStyles : dayStyles}>
            3
          </button>
        </div>
        <div className="buttonContainer">
          <button
            id="+"
            onClick={press}
            className="coloured"
            style={theme ? darkStyles : dayStyles}
          >
            {/* <img src={plus}></img> */} +
          </button>
        </div>
        <div className="buttonContainer">
          <button
            id="00"
            onClick={press}
            style={theme ? darkStyles : dayStyles}
          >
            00
          </button>
        </div>
        <div className="buttonContainer">
          <button id="0" onClick={press} style={theme ? darkStyles : dayStyles}>
            0
          </button>
        </div>
        <div className="buttonContainer">
          <button id="." onClick={press} style={theme ? darkStyles : dayStyles}>
            {" "}
            .
          </button>
        </div>
        <div className="buttonContainer">
          <button
            onClick={getResult}
            className="coloured"
            style={theme ? darkStyles : dayStyles}
          >
            <img src={equalTo}></img>
          </button>
        </div>
      </div>
    </>
  );
}
